img{
    width: 210px;
    height: 280px;
}

h3 {
    margin: 0;
    font-size: 16px;
    font-weight: 500;
    color: #282c3f;
    margin-bottom: 3px;
    margin-block-start: 0 !important;
    line-height: 1;
}

h4{
    margin: 0;
    font-size: 14px;
    color: #535766;
    font-weight: 400;
    line-height: 1;
}

.product-container{
    display: grid;
    grid-template-columns: 210px 210px 210px 210px 210px;
    grid-gap: 35px;
    margin-left: 30px;
    margin-right: 30px;
    margin-top: 20px;
    margin-bottom: 20px;

}

span{
    line-height: 15px;
    margin: 10px 0 6px;
}
