.radio{
    font-size: 10px;
    font-weight: bolder;
    border-right: 1px solid silver;
    border-bottom: 1px solid silver;
    width: 250px;
    padding-top: 20px;
    padding-bottom: 10px;
    padding-right: 30px;
    position: static;
}

.radio > div{
    margin-left: 30px;
    padding-bottom: 5px;
    vertical-align: middle;
}

.radio > p{
    margin-left: 30px;
    margin-bottom: 20px;
}

label{
    display: inline-block;
}

input{
    vertical-align: middle;
}

span{
    vertical-align: middle;
    font-size: 14px;
    color: #282c3f;
    font-weight: normal;
}

.container{
    margin-bottom: 7px;

}

