.topbar {
  display: grid;
  grid-template-columns: 140px 100px 100px 100px 100px 100px auto 70px 70px 70px;
  margin-top: 20px;
  margin-left: 30px;
  margin-right: 30px;
  border-bottom: 1px solid black;
}

.topbar > div {
  text-align: center;
  color: #282c3f;
  padding: 20px 0;
  font-size: 14px;
  font-weight: 500;
  letter-spacing: 0.3px;
  margin-top: auto;
  margin-bottom: auto;
}

.topbar > input {
  margin-top: auto;
  margin-bottom: auto;
  background-color: #f5f5f6;
  border: 1px solid #f5f5f6;
  height: 20px;
  line-height: 24px;
  width: 95%;
  color: #696e79;
  padding: 8px 10px 10px;
  border-radius: 0 4px 4px 0;
}

.myntraLogo > img{
  height: 100px;
  width: 100px;
}

a{
  text-decoration: none;
  color: #282c3f;
}